<template>
  <!--  -->
  <div class="order_content">
   <div class="order_nav" @click="hidepULLDownBox()">
     <div class="posBg">
      <img class="pageBg" :src="pageBg" alt="">
      <ul class="pointTypeBox clear">
        <li class="pointType"><span class="nowPointNum">{{nowPointNum}}</span><span class="nowPointText"></span></li>
        <li class="pointLine"></li>
        <li class="pointType"><span class="totalGainNum">{{totalGainNum}}</span><span class="totalGainText"></span></li>
        <li class="pointLine"></li>
        <li class="pointType"><span class="consumNum">{{consumNum}}</span><span class="consumText"></span></li>
      </ul>
     </div>
   </div>
   <div class="selectBox clear">
     <div class="deal allDealBox fl">
       <div class="allDealHead" @click="allDealClick()">{{selAllDealText?selAllDealText:''}}<span class="dealIcon allDealIcon"></span></div>
       <div class="pullDownBox" v-show="allDealDownBox">
         <ul class="pullLisBox">
           <li class="pullLisIcon"><i></i></li>
           <li class="pullLis" @click="getAllDealVal({type:'0',name:''})"></li>
           <li class="pullLis" @click="getAllDealVal({type:'1',name:''})"></li>
           <li class="pullLis" @click="getAllDealVal({type:'2',name:''})"></li>
         </ul>
       </div>
     </div>
     <div class="fl dealLine"></div>
     <div class="deal dealTimeBox fl">
       <div class="dealTimeHead" @click="dealTimeClick()">{{selDealTimeText?selDealTimeText:''}}<span class="dealIcon dealTimeIcon"></span></div>
       <div class="pullDownBox" v-show="dealTimeDownBox">
         <ul class="pullLisBox">
           <li class="pullLisIcon"><i></i></li>
           <li class="pullLis" @click="getDealTimeVal({type:'0',name:''})"></li>
           <li class="pullLis" @click="getDealTimeVal({type:'1',name:''})"></li>
           <li class="pullLis" @click="getDealTimeVal({type:'2',name:''})"></li>
           <li class="pullLis" @click="getDealTimeVal({type:'3',name:''})"></li>
         </ul>
       </div>
     </div>
   </div>
   <div class="order_list" @click="hidepULLDownBox()">
    <div class="loading" v-if="showLoad">
      <div class="loading_main clear">
        <van-loading color="black" /><span>...</span>
      </div>
    </div>
    <van-list
      v-model="loading"
      :finished="finished"
      @load="turnPage"
      :offset="offset"
      >
      <div class="order_empty" v-show="!isShop">
        <p class="noDatas"></p>
      </div>
      <ul class="order_list_main" v-show="isShop">
        <li v-for="item in shopList" :key='item.id' class="clear">
          <span class="approachText fl">{{item.descr}}</span>
          <span class="approachBox fr">
            <span class="pointNum">{{item.points == '0'?'0':item.type == '1'?'+':'-'+item.points}}</span>
            <span class="getTime">{{item.create_time|formatDate}}</span>
          </span>
        </li>
      </ul>
      <div class="no_data_info clear" v-show="dataFlag">...</div>
    </van-list>
   </div>
  </div>
</template>
<script>
import {formatDate} from '@/components/mine/cardBag/date.js'
import pageBg from '@/assets/test.png'
import {Dialog} from 'vant'
import './index.less'
export default {
  name: 'pointDetial',
  data () {
    return {
      pageBg: pageBg,//
      userid: '',//id
      nowPointNum: '',//
      totalGainNum: '',//
      consumNum: '',//
      selAllDealText: '',//
      selDealTimeText: '',//
      allDealDownBox: false,//
      dealTimeDownBox: false,//
      type: '0',//
      dateType: '0',//
      loading: false,
      finished: false,
      shopList: [],
      showLoad: false,
      offset: Number(10),
      isShop: true,
      pageNo:0, //
      dataFlag: false,//
    }
  },
  mounted: function (){
    document.addEventListener('click', (e) => {//
       if (!this.$el.contains(e.target)) this.allDealDownBox = false
    })
    window.addEventListener('scroll', (e) => {//
      this.allDealDownBox = false//
      this.dealTimeDownBox = false//
    })
  },
  created () {
    this.userid = String(this.$route.query.userid)
    this.$store.dispatch('commonService',{"apid": 54,//
      params: {// 
        id: String(this.$route.query.userid),//id
        type: String(this.type),//
        dateType: String(this.dateType),//
        pageNo: '1',
        pageSize: '10'
    }})
    .then((result) => {
      this.nowPointNum = result.apidata.data.all.uScoreBalance //
      this.totalGainNum = result.apidata.data.all.uScoreAll //
      this.consumNum = result.apidata.data.all.uScoreExpenseAll //
    })
    .catch((error) => {

    });
  },
  methods: {
    routergo: function () {
      this.$router.back(-1)
    },
    allDealClick: function (){
      this.dealTimeDownBox = false//
      this.allDealDownBox = true//
    },
    dealTimeClick: function (){
      this.allDealDownBox = false//
      this.dealTimeDownBox = true//
    },
    getAllDealVal: function (data){//
      this.selAllDealText = data.name//
      this.allDealDownBox = false//
      this.type = data.type// 
      this.pageNo = 0
      this.shopList = []
      this.finished = false
      this.turnPage()
    },
    getDealTimeVal: function (data){//
      this.selDealTimeText = data.name//
      this.dealTimeDownBox = false//
      this.dateType =data.type// 
      this.pageNo = 0
      this.shopList = []
      this.finished = false
      this.turnPage()
    },
    hidepULLDownBox: function (){
      this.allDealDownBox = false//
      this.dealTimeDownBox = false//
    },
    turnPage () {
      this.pageNo += 1;
      const data = {
        apid:"54",
        params:{
          pageNo: String(this.pageNo),
          id: String(this.$route.query.userid),//id
          type: this.type,//
          dateType: this.dateType,//
          pageSize: '10'
        }
      };
      this.loadData(data)  //''
    },
    loadData (data) {
      const _this = this;
      const defaultData = {apid:"54",params:{
        'pageNo':'1',
        'pageSize':'10',
        'status':_this.pageStatus,
        }};
      if(!data){
        data = defaultData;
      }
      this.$store.dispatch('commonService',data).then(function(res){
        if(res.apidata.data.dataInfo.datas.length>0){  //
          _this.shopList =_this.shopList.concat(res.apidata.data.dataInfo.datas);
          _this.isShop = true;
          _this.loading = false;
          if( _this.shopList.length < 10  ) {
              _this.finished = true;
              _this.dataFlag = false
          }
        }else{ //
          _this.finished = true;
          _this.loading = false;
          if(_this.shopList.length <1 && res.apidata.data.dataInfo.datas.length <1 ){
            _this.isShop = false;
            _this.dataFlag = false;
          }else{
            if(_this.shopList.length > 9) {
              _this.dataFlag = true
            } else {
              _this.dataFlag = false
            }
          }
        }
      _this.showLoad = false;
        }).catch((error) => {

        });
    },
    handleScroll () {
      window.pageYOffset = 0
      document.documentElement.scrollTop = 0
      document.body.scrollTop = 0
    }
  },
  filters: {
    formatDate(time) {
        var date = new Date(time);
         return formatDate(date, 'yyyy-MM-dd hh:mm:ss');
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



